/* mobSlider */
.mobSlider{
  max-width: 600px;
  margin: 0 auto;
}
.mobSlider .swiper {
  width: 100%;
  height: 100%;
}
.mobSlider .swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
}
.mobSlider .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.mobSlider .swiper {
  width: 100%;
  height: 300px;
  margin-left: auto;
  margin-right: auto;
}
.mobSlider .swiper-slide {
  background-size: cover;
  background-position: center;
}
.mobSlider .mySwiper2  {
  height: 80%;
  width: 100%;
}
.mobSlider .mySwiper {
  height: 20%;
  box-sizing: border-box;
  padding: 10px 0;
}

.mobSlider .mySwiper .swiper-slide {
  width: 25%;
  height: 100%;
  opacity: 0.4;
}

.mobSlider .mySwiper .swiper-slide-thumb-active {
  opacity: 1;
}

.mobSlider .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}